import * as React from 'react';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Container from '@mui/material/Container';
import Select from '@mui/material/Select';
import { MenuItem, TextField, Typography } from '@mui/material';
import Button from '@mui/material/Button';
import DeleteIcon from '@mui/icons-material/Delete';
import SaveIcon from '@mui/icons-material/Save';
import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye';
import EditIcon from '@mui/icons-material/Edit';
import { useState } from 'react';
import axios from 'axios';
import { useParams } from 'react-router';
import { useNavigate, useLocation } from "react-router-dom";
import { useSelector } from 'react-redux';
import { format } from 'date-fns';
import { FormattedMessage } from "react-intl";
import { useFormik } from 'formik';
import * as yup from 'yup';
import { FormHelperText } from '@mui/material'

const validationSchema = yup.object({
    location: yup.string().required("Location is required"),
    date: yup.date().required("Date is required"),
    type: yup.string().required("Holiday type is required"),
    description: yup.string().required("Description is required")
})

export default () => {
    const { idFromPath } = useParams();
    const path = useLocation();
    const isView = path.pathname.includes('view')

    let holiday = useSelector(store => store.data.holidays.find(holiday => holiday.id == idFromPath)) || {}
    const selectedYear = useSelector(store => store.selectedYear.selectedYear)
    const [id, setId] = useState(holiday.id || '#')
    const [displayDate, setDisplayDate] = useState(holiday.displayDate)
    const [message, setMessage] = useState()
    const [errorMessage, setErrorMessage] = useState()
    const navigate = useNavigate();
    const baseUrl = "http://10.0.3.229"

    const formik = useFormik({
        initialValues: {
            id: holiday.id || '#',
            location: holiday.location,
            description: holiday.description,
            type: holiday.type,
            date: holiday.holiday
        },
        onSubmit: values => {
            saveHoliday(values)
        },
        validationSchema: validationSchema
    })

    const handleDelete = () => {
        axios.delete(`${baseUrl}/holiday/${id}`).then((response) => {
            const year = format(new Date(formik.values.date.split('-')), 'yyyy')
            navigate(`/${year}`)

        }).catch((error) => {
            setErrorMessage(error.response.data.errorMessage)
            setMessage('')
        })
    }

    const handleViewHoliday = () => {
        axios.get(`${baseUrl}/holiday/${id}`).then((response) => {
            let holiday = response.data.holidays;
            formik.values.location = holiday.location
            formik.values.type = holiday.type
            formik.values.date = holiday.holiday
            formik.values.description = holiday.description
            let date = new Date(holiday.holiday.split('-'));
            setDisplayDate(format(date, 'MM-dd-yyyy'))
            navigate(`/view/${id}`)
            setMessage('')
            setErrorMessage('')

        }).catch((error) => {
            setErrorMessage(error.response.data.errorMessage)
            setMessage('')
        })
    }

    const handleUpdateHoliday = () => {
        navigate(`/holiday/${id}`)
    }

    const handleGoToHoliday = () => {
        navigate(`/${selectedYear}`)
    }

    const saveHoliday = (data) => {
        const holiday = {
            id: data.id,
            location: data.location,
            description: data.description,
            type: data.type,
            holiday: data.date
        }
        if (id == '#') {
            axios.post(`${baseUrl}/holiday`, holiday).then((response) => {
                if(response.data.status > 201){                  
                    setErrorMessage(response.data.errorMessage);                    
                    setMessage('')
                } else {
                    let holidays = response.data.holidays;
                    setId(holidays.id)
                    formik.values.id = holidays.id
                    let date = new Date(holidays.holiday.split('-'));
                    setDisplayDate(format(date, 'MM-dd-yyyy'))
                    setMessage(response.data.successMessage)
                    setErrorMessage('')
                }
            })
            .catch((error) => {
                if (error.message.includes('Network')) {
                    setErrorMessage('Backend Service is down')
                }
                setMessage('')
            })
        } else {
            axios.put(`${baseUrl}/holiday/${id}`, holiday).then((response) => {
                if(response.data.status > 201){                 
                    setErrorMessage(response.data.errorMessage);                    
                    setMessage('')
                } else {
                    const holidayData = response.data.holidays
                    let date = new Date(holidayData.holiday.split('-'));
                    setDisplayDate(format(date, 'MM-dd-yyyy'))
                    setMessage(response.data.successMessage)
                    setErrorMessage('')
                }
            })
            .catch((error) => {
                if (error.message.includes('Network')) {
                    setErrorMessage('Backend Service is down')
                }
                setMessage('')
            })
        }
    }

    return (
        <Container maxWidth="lg">
            <Box sx={{ flexGrow: 1 }} >
                <form onSubmit={formik.handleSubmit}>
                    <Grid spacing={1} container justifyContent="center" border={1} mt={1} padding={1}  >
                        <Grid item xs={2}>
                            <Typography sx={{ textAlign: 'right', m: 1 }}>
                                <FormattedMessage id="holiday-id" defaultMessage={"Holiday ID"} />:</Typography>

                        </Grid>
                        <Grid item xs={10}>
                            <TextField name='id' value={formik.values.id} size='small' fullWidth={true} inputProps={{ readOnly: true }} sx={{ bgcolor: '#eceff1' }} />
                        </Grid>

                        <Grid item xs={2}>
                            <Typography sx={{ textAlign: 'right', m: 1 }}>
                                <FormattedMessage id="location" defaultMessage={"Location"} />:
                            </Typography>
                        </Grid>
                        <Grid item xs={10}>
                            {!isView &&
                                <div>
                                    <Select name='location' value={formik.values.location}
                                        error={formik.touched.location && Boolean(formik.errors.location)}
                                        onChange={formik.handleChange} size='small' fullWidth={true}>
                                        <MenuItem value='India' key='India'><FormattedMessage id="india" defaultMessage={"India"} /></MenuItem>
                                        <MenuItem value='All Locations' key='All Locations'><FormattedMessage id="all-locations" defaultMessage={"All Locations"} /></MenuItem>
                                        <MenuItem value='Bettendorf, IA' key='Bettendorf, IA'><FormattedMessage id="bettendorf-iowa" defaultMessage={"Bettendorf, IA"} /></MenuItem>
                                    </Select>
                                    <FormHelperText sx={{ color: '#d32f2f', mx: '13px' }}>{formik.touched.location && formik.errors.location}</FormHelperText>
                                </div>}
                            {isView && <TextField inputProps={{ readOnly: true }} sx={{ bgcolor: '#eceff1' }} name='location' value={formik.values.location} size='small' fullWidth={true} />}
                        </Grid>

                        <Grid item xs={2}>
                            <Typography sx={{ textAlign: 'right', m: 1 }}>
                                <FormattedMessage id="date" defaultMessage={"Date"} />:
                            </Typography>
                        </Grid>
                        <Grid item xs={10}>
                            {!isView && <TextField name='date' value={formik.values.date}
                                error={formik.touched.date && Boolean(formik.errors.date)}
                                helperText={formik.touched.date && formik.errors.date}
                                onChange={formik.handleChange} size='small' fullWidth={true} type='date' />}
                            {isView && <TextField inputProps={{ readOnly: true }} sx={{ bgcolor: '#eceff1' }} name='date' value={displayDate} size='small' fullWidth={true} />}
                        </Grid>

                        <Grid item xs={2}>
                            <Typography sx={{ textAlign: 'right', m: 1 }}><FormattedMessage id="holiday-type" defaultMessage={"Holiday Type"} />:</Typography>
                        </Grid>
                        <Grid item xs={10}>
                            {!isView &&
                                <div>
                                    <Select name='type' value={formik.values.type}
                                        error={formik.touched.type && Boolean(formik.errors.type)}
                                        helperText={formik.touched.type && formik.errors.type}
                                        onChange={formik.handleChange} size='small' fullWidth={true}>
                                        <MenuItem value='Optional' key='Optional'><FormattedMessage id="optional" defaultMessage={"Optional"} /></MenuItem>
                                        <MenuItem value='Fixed' key='Fixed'><FormattedMessage id="fixed" defaultMessage={"Fixed"} /></MenuItem>
                                    </Select>
                                    <FormHelperText sx={{ color: '#d32f2f', mx: '13px' }}>{formik.touched.type && formik.errors.type}</FormHelperText>
                                </div>
                            }
                            {isView && <TextField inputProps={{ readOnly: true }} sx={{ bgcolor: '#eceff1' }} name='type' value={formik.values.type} size='small' fullWidth={true} />}
                        </Grid>

                        <Grid item xs={2} sx={{ mb: 2 }}>
                            <Typography sx={{ textAlign: 'right', m: 1 }}><FormattedMessage id="description" defaultMessage={"Description"} />:</Typography>
                        </Grid>
                        <Grid item xs={10}>
                            {!isView && <TextField name='description' value={formik.values.description}
                                error={formik.touched.description && Boolean(formik.errors.description)}
                                helperText={formik.touched.description && formik.errors.description}
                                onChange={formik.handleChange} size='small' fullWidth={true} placeholder="Description" />}
                            {isView && <TextField inputProps={{ readOnly: true }} sx={{ bgcolor: '#eceff1' }} name='description' value={formik.values.description} size='small' fullWidth={true} placeholder="Description" />}
                        </Grid>

                        <Grid item xs={12} sx={{ mb: 2, display: 'flex', justifyContent: 'center' }}>
                            <Typography sx={{ color: 'green' }}>{message}</Typography>
                            <Typography sx={{ color: 'red' }}>{errorMessage}</Typography>
                        </Grid>


                        <Grid item xs={12} sx={{ display: 'flex', justifyContent: 'center' }}>
                            {!isView && <Button type='submit' variant="contained" bgcolor="blue" startIcon={<SaveIcon sx={{ color: '#ffea00' }} />}>
                                <Typography sx={{ fontSize: 'small', textTransform: 'none' }}><FormattedMessage id="save" defaultMessage={"Save"} /></Typography>
                            </Button>}

                            {!isView && id !== '#' && <div><Button onClick={handleDelete} variant="contained" color="error" sx={{ mx: 1 }} startIcon={<DeleteIcon sx={{ color: '#ffea00' }} />}>
                                <Typography sx={{ fontSize: 'small', textTransform: 'none' }}><FormattedMessage id="delete" defaultMessage={"Delete"} /></Typography>
                            </Button>
                                <Button onClick={handleViewHoliday} variant="outlined" sx={{ borderColor: '#00bcd4' }} startIcon={<RemoveRedEyeIcon sx={{ color: '#1a237e' }} />}>
                                    <Typography sx={{ fontSize: 'small', textTransform: 'none', color: '#00bcd4' }}><FormattedMessage id="switch-to-view" defaultMessage={"Switch to View"} /></Typography>
                                </Button></div>}
                            {isView && <Button onClick={handleUpdateHoliday} variant="outlined" sx={{ borderColor: '#00bcd4' }} startIcon={<EditIcon sx={{ color: '#1a237e' }} />}>
                                <Typography sx={{ fontSize: 'small', textTransform: 'none', color: '#00bcd4' }}><FormattedMessage id="switch-to-update" defaultMessage={"Switch to Update"} /></Typography>
                            </Button>}
                            <Button variant="outlined" onClick={handleGoToHoliday} sx={{ mx: 1, borderColor: '#00bcd4' }}>
                                <Typography sx={{ fontSize: 'small', textTransform: 'none', color: '#00bcd4' }}><FormattedMessage id="goto-holiday" defaultMessage={"Goto: Holidays"} /></Typography>
                            </Button>
                        </Grid>
                    </Grid>
                </form>
            </Box>
        </Container>
    );
}