import * as React from "react";
import { Typography, Link } from "@mui/material";
import Button from "@mui/material/Button";
import HouseIcon from "@mui/icons-material/House";
import FormatListBulletedIcon from "@mui/icons-material/FormatListBulleted";
import SportsFootballIcon from "@mui/icons-material/SportsFootball";
import BeachAccessIcon from "@mui/icons-material/BeachAccess";
import AccountTreeIcon from "@mui/icons-material/AccountTree";
import AttachMoneyIcon from "@mui/icons-material/AttachMoney";
import { useNavigate } from "react-router-dom";
import { FormattedMessage } from "react-intl";

const ClockTime = () => {
  return (
    <Button sx={{ color: "white", m: 0 }}>
      <HouseIcon sx={{ color: "#1a237e", mr: 0.5, display: "inline" }} />
      <Typography sx={{ display: "inline", textTransform: "none" }}>
        <FormattedMessage id="clock-time" defaultMessage={"Clock Time"} />
      </Typography>
    </Button>
  );
};

const Timelog = () => {
  return (
    <Button sx={{ color: "white", m: 0 }}>
      <FormatListBulletedIcon
        color="success"
        sx={{ mr: 0.5, display: "inline" }}
      />
      <Typography sx={{ display: "inline", textTransform: "none" }}>
        <FormattedMessage id="Timelog" defaultMessage={"Timelog"} />
      </Typography>
    </Button>
  );
};

const Vacation = () => {
  return (
    <Button sx={{ color: "white", m: 0 }}>
      <SportsFootballIcon
        sx={{ color: "brown", mr: 0.5, display: "inline" }}
      />
      <Typography sx={{ display: "inline", textTransform: "none" }}>
        <FormattedMessage id="vacation" defaultMessage={"Vacation"} />
      </Typography>
    </Button>
  );
};

const Holiday = () => {
  return (
    <Button component={Link} href="/" sx={{ color: "white", m: 0 }}>
      <BeachAccessIcon
        sx={{ color: "maroon", mr: 0.5, display: "inline" }}
      />
      <Typography sx={{ display: "inline", textTransform: "none" }}>
        <FormattedMessage id="holiday" defaultMessage={"Holiday"} />
      </Typography>
    </Button>
  );
};

const ProjectTime = () => {
  return (
    <Button sx={{ color: "white", m: 0 }}>
      <AccountTreeIcon
        sx={{ color: "#1b5e20", mr: 0.5, display: "inline" }}
      />
      <Typography sx={{ display: "inline", textTransform: "none" }}>
        <FormattedMessage id="project-time" defaultMessage={"Project Time"} />
      </Typography>
    </Button>
  );
};

const TimeSummary = () => {
  return (
    <Button sx={{ color: "white", m: 0, cursor: 'pointer' }}>
      <AttachMoneyIcon
        sx={{ color: "maroon", display: "inline" }}
      />
      <Typography sx={{ display: "inline", textTransform: "none" }}>
        <FormattedMessage id="time-summary" defaultMessage={"Time Summary"} />
      </Typography>
    </Button>
  );
};

export { ClockTime, Timelog, Vacation, Holiday, ProjectTime, TimeSummary };
