import * as React from 'react';
import MenuItem from "@mui/material/MenuItem";
import Select from '@mui/material/Select';
import { getAllHolidaysByYear } from '../slices/holidaySlice';
import { setSelectedYear } from '../slices/selectedYearSlice';

import { useDispatch } from 'react-redux';
import { useState } from 'react';
import { useParams } from "react-router-dom";

const FilterYear = () => {
  const dispatch = useDispatch()
  let { year } = useParams();
  const [selectedYear, setSelectedYearInState] = useState(year || new Date().getFullYear())

  const getYears = () => {
    const currentYear = new Date().getFullYear();
    let yearsArr = []
    for (let i = -3; i < 4; i++) {
      yearsArr.push(currentYear + i);
    }
    return yearsArr;
  };
  const [years] = useState(getYears());

  const handleOnChangeYear = (event) => {
    const year = event.target.value;
    setSelectedYearInState(year)
    dispatch(getAllHolidaysByYear(year))
    dispatch(setSelectedYear(year))
  }
  return (
    <Select value={selectedYear} onChange={handleOnChangeYear} sx={{ bgcolor: "white", width: 250 }}>
      {years.map((year) => (
        <MenuItem key={year} value={year}>{year}</MenuItem>
      ))}
    </Select>
  )
}

export default FilterYear;