import { Typography } from "@mui/material";
import * as React from 'react';
import { useSelector } from 'react-redux';

const DisplayError = () => {
    const error = useSelector(store => store.data.errorMessage)
    return (
        <Typography sx={{ color: 'red', textAlign: 'center', m: 2 }}>
            {error}
        </Typography>
    )
}
export default DisplayError;