import { createAsyncThunk, createSlice } from '@reduxjs/toolkit'
import axios from 'axios';

import { format } from 'date-fns'

export const getAllHolidaysByYear = createAsyncThunk(
    'data/getAllHolidaysByYear',
    async (year) => {
        try {
            const response = await axios.get(`http://10.0.3.229/holiday/year/${year}`)
            if(response.data.status > 200){
                return {
                    holidays: response.data.holidays || [],
                    errorMessage: response.data.errorMessage
                }
            }
            let holidays = response.data.holidays;
            holidays.forEach((holiday) => {
                let date = new Date(holiday.holiday.split('-'));
                holiday.displayDate = format(date, 'MM-dd-yyyy');
            });
            response.data.holidays = holidays
            return response.data
        } catch (error) {
            if (error.message.includes('Network')) {
                return {
                    holidays: [],
                    errorMessage: "Backend service is down"
                }
            } else {
                return {
                    holidays: error.response.data.holidays || [],
                    errorMessage: error.response.data.errorMessage
                }
            }
        }
    }
)

const initialState = {}
const holidaySlice = createSlice({
    name: 'data',
    initialState,
    reducers: {
    },
    extraReducers: (builder) => {
        builder.addCase(getAllHolidaysByYear.pending, (state, action) => {
            state.holidays = []
        })

        builder.addCase(getAllHolidaysByYear.fulfilled, (state, action) => {
            state.holidays = action.payload.holidays
            state.errorMessage = action.payload.errorMessage
        })
    },
})

export default holidaySlice.reducer