import React from 'react';
import ReactDOM from 'react-dom';
import merge from 'deepmerge';
import './index.css';
import HolidayRoutes from './HolidayRoutes';
import reportWebVitals from './reportWebVitals';
import { IntlProvider } from "react-intl";
import English from './languages/en-US.json';
import Hindi from './languages/hi-IN.json';
import Marathi from './languages/mr-IN.json';



const locale = navigator.language;

let lang = English;
if (locale === "mr") {
  lang = merge.all([English, Hindi, Marathi ]);
} else if (locale === "hi") {
    lang = merge.all([English, Hindi]);
}

ReactDOM.render(<IntlProvider locale={locale} messages={lang}>
  <React.StrictMode>
    <HolidayRoutes />
  </React.StrictMode>
</IntlProvider>,
  document.getElementById('root')
);

reportWebVitals();
