import * as React from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import Navbar from './components/Navbar';
import HolidayList from './components/HolidayList';
import HolidayForm from './components/HolidayForm';
import configureStore from './store/configureStore';
import { Provider } from 'react-redux';

const store = configureStore();
const HolidayRoutes = () => (
  <Provider store={store} >
    <BrowserRouter>
      <div>
        <Navbar />
        <Routes>
          <Route path="/" element={<HolidayList />} exact={true} />
          <Route path="/:year" element={<HolidayList />} exact={true} />
          <Route path="/holiday" element={<HolidayForm />} />
          <Route path="/holiday/:idFromPath" element={<HolidayForm />} />
          <Route path="/view/:idFromPath" element={<HolidayForm />} />
        </Routes>
      </div>
    </BrowserRouter>
  </Provider>
);
export default HolidayRoutes;



