import * as React from 'react';
import { IconButton, Typography } from "@mui/material";
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye';
import EditIcon from '@mui/icons-material/Edit';
import Paper from '@mui/material/Paper';
import Box from "@mui/material/Box";
import { useNavigate, useParams } from "react-router-dom";
import { getAllHolidaysByYear } from '../slices/holidaySlice';
import { useDispatch, useSelector } from 'react-redux';
import { useEffect } from 'react';
import FilterYear from './FilterYear';
import DisplayError from './DisplayError'
import { FormattedMessage } from "react-intl";

const HolidayList = () => {
  const dispatch = useDispatch()
  const navigate = useNavigate();
  let { year } = useParams();

  useEffect(() => {
    dispatch(getAllHolidaysByYear(year || new Date().getFullYear()))
  }, [])

  const handleAddHoliday = () => {
    navigate("/holiday")
  };

  const handleEditHoliday = (id) => {
    navigate(`/holiday/${id}`)
  };

  const handleViewHoliday = (id) => {
    navigate(`/view/${id}`)
  }
  let holidays = []
  const data = useSelector(store => store.data)
  if (data.holidays) {
    holidays = data.holidays
  }

  return (
    <div>
      <Box sx={{ display: 'flex', bgcolor: "#17a2b8", width: '100%', height: 35 }}>
        <Typography sx={{ color: 'white', ml: 22, mr: 5, mt: .5 }} >
          <FormattedMessage id="filter-year" defaultMessage={"Filter-Year :"} />
        </Typography>

        <FilterYear />
      </Box>
      <TableContainer component={Paper} sx={{ boxShadow: 'none', borderRadius: 0 }}>
        <Table
          sx={{
            minWidth: 650,
            [`& .${tableCellClasses.root}`]: {
              borderBottom: "none"
            },
            m: 0
          }} aria-label="simple table">
          <TableHead>
            <TableRow sx={{ bgcolor: 'black' }}>
              <TableCell sx={{ color: 'white', py: 0, pl: 10, width: 5 }}>
                <Typography sx={{ display: 'inline' }}>
                  <FormattedMessage id="action" defaultMessage={"Action"} />
                </Typography>
                <Typography sx={{ display: 'inline' }}>
                  <IconButton onClick={handleAddHoliday}>
                    <AddCircleIcon sx={{ color: "#ffeb3b", fontSize: 'medium' }} />
                  </IconButton>
                </Typography>
              </TableCell>

              <TableCell sx={{ color: 'white', py: 0, width: 30 }}>
                <FormattedMessage id="date" defaultMessage={"Date"} />
              </TableCell>
              <TableCell align="left" sx={{ color: 'white', py: 0, width: 300 }}><FormattedMessage id="description" defaultMessage={"Description"} /></TableCell>
              <TableCell align="left" sx={{ color: 'white', py: 0, width: 50 }}><FormattedMessage id="type" defaultMessage={"Type"} /></TableCell>
              <TableCell align="left" sx={{ color: 'white', py: 0, width: 250 }}><FormattedMessage id="location" defaultMessage={"Location"} /></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>

            {holidays.map((row) => (
              <TableRow
                key={row.id}
              >
                <IconButton disableRipple onClick={() => handleViewHoliday(row.id)} sx={{ pl: 10, py: 0 }}>
                  <RemoveRedEyeIcon sx={{ fontSize: 'medium', color: '#00838f' }} />
                </IconButton>

                <IconButton disableRipple onClick={() => handleEditHoliday(row.id)}>
                  <EditIcon sx={{ fontSize: 'medium', ml: 1, color: '#bf360c' }} />
                </IconButton>

                <TableCell sx={{ py: 0 }}>
                  {row.displayDate}
                </TableCell>
                <TableCell align="left" sx={{ py: .5 }}>{row.description}</TableCell>
                <TableCell align="left" sx={{ py: .5 }}>{row.type}</TableCell>
                <TableCell align="left" sx={{ py: .5 }}>{row.location}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <DisplayError />
    </div>
  );
}

export default HolidayList;
